input[type=text] {
    width: 0px;
    box-sizing: border-box;
    border: 2px solid rgba(0, 0, 0, 0);
    border-radius: 4px;
    font-size: 16px;
    background-color: rgba(0,0,0,0);
    padding: 0px 8px 0px 0px;
    transition: width 0.4s ease-in-out;
  }
  
  /* When the input field gets focus, change its width to 100% */
  input[type=text]:focus {
    width: 100%;
    margin-left: 20px;
    outline: none;
    color:whitesmoke;
  }

/* pc screen */
@media only screen and (min-width: 720px) {
    .navigation{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        max-width: 1080px;
    
    }
    .header{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .logoAndButton{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .navButton{
        display: none;
        background-color:rgba(0,0,0,0);
        border: none;
    }
    .slide{
        display: flex
    
    }
    .searchButton{
        display: flex;
        background-color:rgba(0,0,0,0);
        border: none;
        margin-left: 20px;

    }

    .noneForPc{
        display: none;
    }
  }

  /* mobile screen */
  @media only screen and (max-width: 720px) {
    .navigation{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    
    }
    .header{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .logoAndButton{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
    }
    .slide{
        display: none
    }
    .searchButton{
        display: none
    }
    .navButton{
        background-color:rgba(0,0,0,0);
        border: none;
    }
    .noneForPc{
        display: flex;
    }

    .searchMobile{
        width: 100%;
        border-radius: 5px;
        background-color: rgba(0, 0, 0, 0.18);
        border-color: rgba(0, 0, 0, 0.4);
        padding:5px;
        color: whitesmoke;
    }
  }

