.Sbutton{
    position: absolute;
    right: calc(4% + 1px);
    display: flex;
    outline: 0;
    transition: all .5s;
    border-radius: 35px;
    z-index: 1000;
    border: 0;
    /* background: rgba(0,0,0,0.5); */
    min-width: 43px;
    min-height: 43px;
    opacity: 1;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    background-color: transparent;
}

.SbuttonLeft{
    position: absolute;
    left: calc(4% + 1px);
    display: flex;
    outline: 0;
    transition: all .5s;
    border-radius: 35px;
    z-index: 1000;
    border: 0;
    /* background: rgba(0,0,0,0.5); */
    min-width: 43px;
    min-height: 43px;
    opacity: 1;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    background-color: transparent;

}

